.contact-container {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: var(--warna-cream);
  color: var(--warna-biru);
}
.contact-container .upper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 3rem;
}
.contact-container .upper h1 {
  max-width: 64rem;
  width: 100%;
  font-size: 4rem;
  padding-top: 6rem;
}
.contact-container .upper form {
  max-width: 64rem;
  display: flex;
  flex-direction: column;
  padding-bottom: 8rem;
  width: 100%;
}
.contact-container .upper form label {
  padding-left: 0.25rem;
  text-transform: uppercase;
}
.contact-container .upper form textarea {
  font-family: var(--font-inter);
  resize: none;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid var(--warna-biru);
  font-size: 1.25rem;
  width: 100%;
  color: var(--warna-biru);
}
.contact-container .upper form textarea:focus {
  outline: none;
}
.contact-container .upper form textarea:focus::placeholder {
  color: transparent;
}
.contact-container .upper form textarea::placeholder {
  color: var(--warna-biru);
  opacity: 1;
}
.contact-container .upper form input {
  position: relative;
  width: 100%;
  border: none;
  border-bottom: 1px solid var(--warna-biru);
  background-color: transparent;
  opacity: 1;
  font-size: 1.25rem;
  color: var(--warna-biru);
}
.contact-container .upper form input:focus {
  outline: none;
}
.contact-container .upper form input:focus::placeholder {
  color: transparent;
}
.contact-container .upper form input::placeholder {
  color: var(--warna-biru);
  opacity: 1;
}
.contact-container .upper form .nama {
  position: relative;
  margin-top: 4rem;
}
.contact-container .upper form .nama label {
  position: absolute;
}
.contact-container .upper form .email {
  position: relative;
  margin-top: 4rem;
}
.contact-container .upper form .email label {
  position: absolute;
}
.contact-container .upper form .pesan {
  position: relative;
  margin-bottom: 4rem;
  margin-top: 4rem;
}
.contact-container .upper form .pesan label {
  z-index: 0;
  position: absolute;
}
.contact-container .upper form button {
  font-size: 1rem;
  cursor: pointer;
  color: var(--warna-biru);
  width: 6.25rem;
  height: 3.125rem;
  border-radius: 1.875rem;
  background-color: transparent;
  border: 2px solid var(--warna-biru);
  transition: all 0.3s ease;
}
.contact-container .upper form button:hover {
  background-color: var(--warna-biru);
  color: var(--warna-cream);
}
.contact-container .upper form #kirim-container {
  font-weight: 400;
  width: 9.375rem;
  text-transform: uppercase;
  z-index: 1;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  border-radius: 40px;
  padding: 0.5rem 2rem;
  border: 1px solid var(--warna-biru);
}
.contact-container .upper form #kirim-container .kirim {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.contact-container .upper form #kirim2-container {
  font-weight: 400;
  width: 9.375rem;
  text-transform: uppercase;
  z-index: 0;
  position: absolute;
  cursor: pointer;
  transform: translateY(-46px);
  padding: 0.5rem 2rem;
}
.contact-container .upper form #kirim2-container .kirim2 {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.contact-container footer {
  position: relative;
  bottom: 0;
}
.contact-container footer .footer-container {
  width: 100vw;
  height: 10rem;
  background-color: var(--warna-biru);
}
.contact-container footer .footer-container h1 {
  color: var(--warna-cream);
}